<template>
  <div class="hello">
    <ul>
    	<li v-for="item in list.aka_en">{{item}}
    	<p>{{item.name}}</p>
    	</li>
    	<li v-for="item1 in list.works">
    		<p>{{item1.roles}}{{item1.subject.genres}}
    		</p>
    	</li>
    	<li v-for="item2 in list.avatars" class="aImg">
    	<img :src="item2"/>
    	</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      list:[
      		
      ]
    }
  },
  mounted:function(){
			this.$http.jsonp('https://api.douban.com/v2/movie/celebrity/1054395').then(function(res){
				this.list=res.body;
			},function(){
				alert("请求失败")
			});
	}
}
</script>
<style scoped>
.aImg>img{float: left;height: 200px;}
</style>



