<template>
  <div class="hello">
    <ul>
    	<li v-for="item in list">{{item.max}}</li>
    	<li v-for="item1 in list.tags">{{item1.count}} {{item1.name}} {{item1.title}}</li>
    	<li v-for="item2 in list.summary">
    		<p>{{item2}}</p>
    	</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      list:[
      	
      ]
    }
  },
  mounted:function(){
			this.$http.jsonp('https://api.douban.com/v2/book/1220562').then(function(res){
				
				this.list=res.body;
				console.log(res)
			},function(){
				alert("请求失败")
			});
	}
}
</script>
<style scoped>
p{float: left;}
</style>



