<template>
  <div class="hello">
    <ul>
    	<li v-for="item in list">{{item.id}}{{item.lis}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      list:[
      			
      ]
    }
  },
  mounted:function(){
			this.$http.get('./static/Hello.json').then(function(res){
				this.list=res.body;
			},function(){
				alert("请求失败")
			});
	}
}
</script>

